@charset 'utf-8';
@import 'rest.scss';
@import 'global.scss';
@import 'public.scss';
@import 'component.scss';
@import './plugins/select.scss';

// 特定样式
body {
  font-family: $Font;
  font-size: 13px;
}


// 没有菜单的 li.active，  有菜单的，点击菜单项 li > a.on
// 导航栏
.nav-bar {
  font-family: $Font;
  font-size: 14px;
  width: 100%;
  height: 100%;
  background-color: $Theme-navigation-bg;
  overflow-y: auto;
  color: $Theme-navigation-text;


  .menu-title, .sub-title  {
    @extend %ellipsis;
    position: relative;
    border-bottom: 1px solid #49515c;
    text-indent: 20px;
    line-height: 49px;
    padding: 0 15px 0 20px;
    cursor: pointer;

    &:hover {
      background-color: $Theme-navigation-line-hover;
    }
  }

  .sub-title {
    background: url($PATH-Image-Dir + 'arrow2_down.png') 10px center no-repeat;
  }


  .active {
    .menu-title .arrow-left{
      display: block;
    }
    .sub-title {
      background: url($PATH-Image-Dir + 'arrow2_top.png') 10px center no-repeat;
    }
    .menus {
      display: block;
    }
  }


  .menus {
    display: none;
    background-color: $Theme-navigation-list-bg;

    a {
      @extend %ellipsis;
      padding-right: 40px;
      line-height: 45px;
      padding-left: 50px;
      color: $Theme-navigation-list-text;
      position: relative;
      display: block;
    }

    a:hover {
      background: $Theme-navigation-line-hover;
    }

    .on {
      color: $Theme-link;
      background: $Theme-navigation-line-hover;
      border-left: 2px solid $Theme-link;

      .arrow-left {
        display: block;
      }

    }

  }


  .arrow-left {
    display: none;
    position: absolute;
    right: 0;
    top: 14px;
    width: 0;
    height: 0;
    border-bottom: 7px solid transparent;
    border-top: 7px solid transparent;
    border-right: 9px solid $Theme-container-bg;
    font-size: 0;
    line-height: 0;;
  }




}

// 首页容器
.home-container {
  @extend %fix;
  padding-top: $Header-Height;
  height: 100%;
}

// tabs选项卡容器
.tabs-out {
  position: relative;
  margin: 0 10px 0 10px;

  .tabs-in {
    position: absolute;
    width: 100%;
    height: 40px;
    margin-top: 5px;
    z-index: 1;
    border-bottom: 1px solid #eee;
  }

}

// tab选项卡左右箭头
.arrow-lr {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;

  .arrow {
    position: absolute;
    top: 0;
    height: 36px;
    width: 19px;
    margin: 2px 4px;
    background: url($PATH-Image-Dir + 'tabs_icons.png') 1px center no-repeat #e0ecff;
    z-index: 4;
    cursor: pointer;

    &:hover {
      background-color: #d5e1f5;
    }
  }

  .arrow-left {
    left: 0;
    margin-left: 0;
  }

  .arrow-right {
    right: 0;
    margin-right: 0;
    background-position: -15px center;
  }


}


/* ===========tab选项卡颜色=========== */
$Theme-man-text-color: #000; /* 主文本 */
$Theme-line-color: rgb(238, 238, 238); /* 分隔线条 */
$Theme-nav-active-color: rgb(76, 141, 238); /*导航菜单激活 */
$Theme-tab-linear-start: rgb(239, 245, 255); /*tab选项卡渐变起始 */
$Theme-tab-linear-end: rgb(224, 236, 255); /*tab选项卡渐变起始 */
$Theme-tab-hover: rgb(213, 225, 245); /*tab选项卡悬停 */
// tab选项卡
.tabs-pill {
  position: relative;
  width: 9999px;

  li {
    float: left;
    margin: 0 0 0 4px;
    padding: 3px 0 0;
    border-bottom: 3px solid transparent;
    cursor: pointer;
  }
  li.selected {
    border-bottom: 3px solid #4c8dee;

    .tabs-inner {
      height: 32px;
      font-weight: 700;
      background: #eee;
      filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#EEEEEE,endColorstr=#EEEEEE);
    }

  }

  .tabs-inner {
    @extend %ellipsis;
    position: relative;
    display: block;
    height: 34px;
    width: 140px;
    padding: 0 10px;
    line-height: 34px;
    color: #000;
    font-size: 14px;
    text-align: center;
    background: linear-gradient(to bottom,$Theme-tab-linear-start 0,$Theme-tab-linear-end 100%);
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#EFF5FF,endColorstr=#E0ECFF);
    &:hover {
      background: $Theme-tab-hover;
      color: $Theme-man-text-color;
      filter: none;
    }

  }

  .first .close {
    display: none;
  }

  .close {
    position: absolute;
    display: block;
    width: 14px;
    height: 14px;
    right: 3px;
    top: 10px;
    opacity: 0.6;
    background: url($PATH-Image-Dir + 'tabs_icons.png') -34px center no-repeat;
    &:hover {
      opacity: 1;
    }
  }

}

// 用于选项卡滚动
.tabs-size {
  @extend %ib;
  position: absolute;
}

// 框架容器
.ctx-window-out {
  overflow: hidden;
  position: relative;
  margin: 0 0 0 10px;
  height: 100%;
  box-sizing: border-box;
  padding-top: 51px;
  background: #f5f5f5;

  .ctx-window-in {
    background-color: white;
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  .load-img {
    filter:progid:DXImageTransform.Microsoft.gradient(startColorstr=#19000000,endColorstr=#19000000);
    background: rgba(0,0,0,.1);
    z-index: 3;

    .img {
      background: url($PATH-Image-Dir + 'loading.gif') center center no-repeat;
    }
  }

  .div-iframe {
    -webkit-overflow-scrolling:touch;
    overflow: auto;
    @extend %full-size;
    opacity: 0;
  }
  .div-iframe.active {
    opacity: 1;
  }

  iframe {
    @extend %full-size;
  }

}

