.uc-process {
  $height: 26px;
  @extend %fix;

  .unit {
    float: left;
    font-size: 14px;
    width: 307px;
    height: $height;
    line-height: $height;
    color: white;
    text-align: center;
  }

  .arrow-complete {
    position: relative;
    float: left;
    height: $height;
    overflow: hidden;
    background: #00a2ca;
    width: 10px;

    .prev {
      border-left-color: #9cddf5;
    }

  }

  .arrow-current {
    position: relative;
    width: 2%;
    overflow: hidden;
    background: #ccc;
    float: left;
    height: $height;

    .prev {
      border-left-color: #00a2ca;
    }

  }



  .next, .prev {
    position: absolute;
    display: block;
    left: 0;
    height: 0;
    width: 0;
    overflow: hidden;
  }

  .next {
    top: -3px;
    border: 10px solid transparent;
    border-width: 16px 10px;
    border-left-color: #fff;
  }

  .prev {
    top: 0;
    border: 10px solid transparent;
    border-width: 13px 8px;
    border-left-color: #ccc;
  }


  .arrow-right {
    float: left;
    font-size: 0;
    line-height: 0;
    border-bottom: 14px solid transparent;
    border-top: 14px solid transparent;
    border-left: 16px solid #9cddf5;
  }


}

