@charset 'utf-8';
@import 'global.scss';

// 公共组件


// 白色链接
.a-white {
  color: $Theme-link-text;
  &:hover {
    color: $Theme-header-link-hover;
  }
}

// 页眉
.header {
  @extend %fix;
  width: 100%;
  min-width: $Min-Width;
  font-family: $Font;
  font-size: 14px;
  height: $Header-Height;
  line-height: $Header-Height;
  background-color: $Theme-link;
  color: $Theme-header-link;

  .logo-box,
  .logo-box img {
    height: 100%;
  }

  .logo-box {
    padding: 5px;
    margin-left: 20px;
  }

  .account {
    @extend %fix;
    margin-right: 20px;
  }

  .links {
    @extend %fix;
    margin-right: 40px;

    li {
      float: left;
      font-size: 12px;
      margin-right: 20px;
    }

    a {
      @extend .a-white;
    }


  }

  .user-plan {
    position: relative;
  }

  .user-plan:hover {
    .arrow-2 {
      background: url($PATH-Image-Dir + 'arrow2.png') center right no-repeat;
      background-position-x: -19px;
    }
    .menus {
      display: block;
    }
  }

  .arrow-2 {
    @extend %ib;
    position: absolute;
    right: 0;
    top: 25px;
    width: 19px;
    height: 11px;
    background: url($PATH-Image-Dir + 'arrow2.png') center right no-repeat;
    background-position-x: 0;
  }

  .title {
    @extend %ib;
    @extend %ellipsis;
    max-width: 200px;
    padding-right: 40px;
  }


  .menus {
    display: none;
    position: absolute;
    top: $Header-Height + 10;
    right: 0;
    background: $Theme-link;
    z-index: 5;

    a {
      display: block;
      padding: 11px 22px;
      line-height: 22px;
      @extend .a-white;
      &:hover {
        background: $Theme-link-hover;
      }
    }


  }

  .triangle {
    display: block;
    position: absolute;
    top: -12px;
    width: 100%;
    height: 12px;
    background: url($PATH-Image-Dir + 'triangle.png') no-repeat 90% center;
  }


}

// 跳转条
.jump-bar {
  @extend %ib;
  font-size: 14px;
  color: #666;

  .count {
    margin: 0 5px;
    color: #03A9F4;
  }

  .jump-number {
    @extend %ib;
  }


  .btn-1,
  .jump-number a {
    color: $Theme-navigation-text;
    height: 30px;
    margin-right: 4px;
    background-color: #fff;
    border: 1px solid #d7dde4;
    border-radius: 4px;
    min-width: 29px;
    line-height: 30px;
    cursor: pointer;
    font-family: Arial;
    display: inline-block;
    text-align: center;

    &:hover {
      border-color: #39f;
    }
  }

  .jump-number .ellips {
    cursor: default;
  }


  .jump-number .current {
    color: #fff;
    background-color: #39f;
    border-color: #39f;
  }

  .input-wrap > .input-text {
    padding-right: 32px;
  }


  .jump-btn {
    position: absolute;
    right: 0;
    top: 0;
    height: 30px;
    margin-right: 0;
    line-height: 30px;
  }


  .input-wrap {
    @extend %ib;
    position: relative;
    width: 70px;
  }

}

// 首页区块
$Color-area-block-bg: rgb(255, 255, 255); /* 区块背景 */
$Color-area-block-line: rgb(234, 231, 231); /* 区块分割线 */
$Color-area-block-text: rgb(51, 51, 51); /* 区块文本 */
.area-block {
  height: 100%;
  background: $Color-area-block-bg;

  .primary {
    @extend %ib;
    @extend %ellipsis;
    max-width: 90%;

  }

  .title {
    color: $Color-area-block-text;
    padding: 5px 20px;
    border-bottom: 1px solid $Color-area-block-line;
    line-height: 23px;

    a {
      color: $Color-area-block-text;
    }
    span {
      font-size: 12px;
    }

  }


  a.active {
    color: $Theme-link;
  }

  .plane {
    min-height: 100px;
    color: $Theme-second-text;
  }

  .line {
    @extend %ib;
    width: 1px;
    height: 12px;
    margin: 2px 10px 0;
    background: #eae7e7;
  }

}

// 公告样式
.bulletin-show {

  .article {
    text-align: left;
    padding: 10px;
    font-size: 19px;
    line-height: 28px;

    .t-2 {
      color: red;
      font-size: 25px;
    }

    p {
      color: rgb(51,51,51);
      margin-bottom: 20px;
    }

  }

  .title {
    text-align: center;
    border-bottom: 1px solid rgb(154, 144, 144);
    padding-bottom: 15px;
  }

  .mt {
    font-size: 35px;
  }

  .date-lable {
    color: red;
    margin-right: 10px;
  }

  .date {
    color: #9a9090;
  }

  ul {
    list-style: inherit;
    padding: 15px;
  }

  li {
    margin-bottom: 20px;
  }

}

// 模态弹出框-通用样式
.modal-wrap,
.modal-mask {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
// 模态弹出框
.modal-wrap {
  overflow: auto;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  outline: 0;

  .modal-close {
    padding: 0 5px 0;
    font-size: 12px;
    position: absolute;
    right: 16px;
    top: 8px;
    overflow: hidden;
    cursor: pointer;
    color: #222222;
  }

  .modal-mask {
    height: 100%;
    background-color: black;
    opacity: 0.3;
    filter: alpha(opacity=30);
  }

  iframe {
    // 去掉max-width就可以悬浮出现底部滚动条
    max-width: 100%;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
  }

  .modal-body {
    -webkit-overflow-scrolling: touch;
    overflow: auto;
  }


  .modal-area {
    transform: scale(0, 0);
    transition: all 0.5s;
    max-width: 90%;
    width: auto;
    margin: 0 auto;
    position: relative;
    top: 0;
    outline: 0;
  }

  .modal-content {
    box-shadow: 3px 3px 10px rgba(0,0,0,0.5);
    position: relative;
    background-color: #fff;
    border: 0;
    border-radius: 6px;
  }

  .modal-header {
    border-bottom: 1px solid #e9e9e9;
    padding: 10px 16px;

    p {
      @extend %ib;
      @extend %ellipsis;
      text-align: center;
      width: 100%;
      height: 20px;
      line-height: 20px;
      font-size: 14px;
      font-weight: 700;
    }

  }

  .modal-footer {
    border-top: 1px solid #e9e9e9;
    padding: 10px 18px 10px 10px;
    text-align: right;
  }



}
// 模态对话框垂直居中
.vertical-center-modal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

// Message消息
.message-wrap {
  font-size: 12px;
  position: fixed;
  z-index: 1010;
  width: 100%;
  top: 16px;
  left: 0;
  text-align: center;

  .message-content {
    @extend %ib;
    text-align: left;
    padding: 8px 16px;
    border: 1px solid #d9d9d9;
    border-radius: 6px;
    box-shadow: 0 1px 6px hsla(0,0%,39%,.2);
    background: #fff;
  }
  .icon {
    margin-right: 8px;
    font-size: 14px;
    top: 1px;
    position: relative;
    color: #0099e5;
  }
  .text {
    color: #525558;
  }

}

// 旋转图标
.icon-rotate {
  -webkit-animation: fa-spin 2s infinite linear;
  animation: fa-spin 2s infinite linear;
}

@-webkit-keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}


// 后端分页控件样式
// <span class="off">上一页&nbsp;<div></div></span> 改  <span class="btn-1"><i class="fa-chevron-left fa"></i></span>
// <a href="xx"><span>下一页&nbsp;</span></a> 改 <span class="btn-1"><i class="fa-chevron-right fa"></i></span>
// <a href="xx"><span>尾页&nbsp;</span></a> 改 <a href="xx">尾页</a> 

// <span class="off">首页&nbsp;</span> 改  <span class="btn-1 off"><i class="fa-step-backward fa"></i></span>
// <a href="xx"><span>尾页&nbsp;</span></a> 改  <a style="border: none;" href="xx"><span class="btn-1 off"><i class="fa-step-forward fa"></i></span></a>
div.gpage {
  margin: 20px 0;
  font-size: 14px;
  color: #666;

  .search_resultsPagingTag {
    margin: auto;
  }

  .search_resultsSummary {
    font-size: 13px;

    b {
      margin: 0 5px;
      color: #03a9f4;
    }

  }

  .search_resultsPaging {

    .off,
    th,
    a {
      color: #999;
      height: 30px;
      margin-right: 4px;
      background-color: #fff;
      border: 1px solid #d7dde4;
      border-radius: 4px;
      min-width: 29px;
      line-height: 30px;
      cursor: pointer;
      font-family: Arial;
      display: inline-block;
      text-align: center;
    }

    th {
      color: #fff;
      background-color: #39f;
      border-color: #39f;
    }
    .off {
      cursor: not-allowed;
    }

  }



}


