@charset 'utf-8';
// 全局样式

/* ===主题颜色=== */
$Theme-link: rgb(3, 169, 244); /* 链接 */
$Theme-link-text: rgb(255, 255, 255); /* 链接文本 */
$Theme-link-hover: rgb(44, 191, 255); /* 链接悬停 */
$Theme-header-link: rgb(255, 255, 255); /* 链接 */
$Theme-header-link-hover: rgb(240, 240, 240); /* 链接悬停 */
$Theme-primary-text: rgb(51, 51, 51); /* 主文本 */
$Theme-second-text: rgb(102, 102, 102); /* 次文本 */
$Theme-navigation-bg: rgb(59, 68, 79); /* 导航栏背景 */
$Theme-navigation-text: rgb(153, 153, 153); /* 导航栏文本 */
$Theme-navigation-hover-bg: rgb(52, 62, 75); /* 导航栏悬停背景 */
$Theme-navigation-list-bg: rgb(44, 54, 69); /* 导航栏列表文本 */
$Theme-navigation-list-text: rgb(216, 216, 216); /* 导航栏列表文本 */
$Theme-navigation-line: rgb(73, 81, 92); /* 导航栏列分割线 */
$Theme-navigation-line-hover: rgb(52, 62, 75); /* 导航栏列分割线悬停 */
$Theme-container-bg: rgb(245, 245, 245); /* 内容背景 */
$Theme-gray: rgb(204, 204, 204);
$Theme-gray-hover: rgb(244, 246, 249);
$Text-primary: rgb(0, 0, 0); /* 主要文本 */
$Text-second: rgb(102, 102, 102); /* 次要文本 */


/* ===环境变量=== */
// 图片目录
$PATH-Image-Dir: "../imgs/";
// 页面最小宽度
$Min-Width: 1020px;
// 页眉高度
$Header-Height: 60px;
$Font: "PingFang SC","Microsoft yahei","Helvetica Neue",Helvetica,Arial,sans-serif;


// 全尺寸样式
%full-size {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

// 内联
%ib {
  display: inline-block;
  zoom: 1; *display: inline;
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: top;
}

// 清除float浮动坍塌
%fix {
  zoom:1;

  &:after {
    display:block;
    content:'.';
    clear:both;
    line-height:0;
    visibility:hidden;
  }

}

// 溢出隐藏
%ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


