@charset 'utf-8';
@import 'global.scss';
// 公共样式
// 每个样式都是最小粒度, 所以可以提取相同的样式来优化代码


* {
  box-sizing: border-box;
}

a,
.btn,
.btn-white,
.swich span,
.input-text,
.input-select,
.table-two-color {
  font-family: $Font;
}

.btn,
.btn-white,
.swich span {
  cursor: pointer;
  font-size: 14px;
}

.btn,
.btn-white,
.input-text,
.input-select {
  border-radius: 3px;
  transition: all .3s;
}


// 链接默认样式
a {
  color: $Theme-link;
  text-decoration: none;
  &:hover {
    color: $Theme-link-hover;
  }
  &.disabled {
    color: gainsboro;
    cursor: not-allowed;
    pointer-events: none;
  }
  &.loading {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.7;
    filter: alpha(opacity=70);
  }
}


// 内联居中
.middle {
  @extend %ib;
  width: 0;
  height: 100%;
  vertical-align: middle;
}

// 清除浮动
.fix {
  @extend %fix;
}

.loading {
  pointer-events: none;
  cursor: not-allowed;
  opacity: 0.7;
  filter: alpha(opacity=70);
}


// 基础按钮
.btn {
  @extend %ib;
  @extend %ellipsis;
  text-align: center;
  padding: 0px 21px;
  height: 30px;
  line-height: 30px;
  border-radius: 4px;
  color: $Theme-link-text;
  border: 1px solid $Theme-link;
  background-color: $Theme-link;

  &:hover {
    color: $Theme-link-text;
    background-color: $Theme-link-hover;
  }
  &.disabled {
    pointer-events: none;
    cursor: not-allowed;
    background-color: gainsboro;
    border: 1px solid gray;
    color: gray;
  }
  &.loading {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.7;
    filter: alpha(opacity=70);
  }
}

// 基础按钮反色
.reverse {
  color: #03a9f4;
  background-color: #fff;
  &:hover {
    color: #03a9f4;
    background-color: #eff5ff;  
  }

}

// 白底按钮
.btn-white {
  padding: 0.3em 1em;
  line-height: 20px;
  border-radius: 4px;
  color: $Theme-link;
  border: 1px solid $Theme-gray;
  background-color: $Theme-header-link;

  &:hover {
    background-color: $Theme-gray-hover;
  }
  &.disabled {
    pointer-events: none;
    cursor: not-allowed;
    background-color: gainsboro;
    border: 1px solid gray;
    color: gray;
  }
}

// 文本框
.input-text {
  font-size: 12px;
  width: 100%;
  height: 30px;
  line-height: 30px;
  border: 1px solid #d3d3d3;
  padding: 0 10px;
  display: block;

  &:focus {
    border-color: rgba(102,175,233,.5);
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 7px rgba(102,175,233,.6);
  }

}

// 必填型号
.behoove {
  font-size: 12px;
  color: red;
  font-weight: bold;
  font-family: serif;
}

.verif-message {
  font-size: 12px;
  color: red;
  font-weight: bold;
  font-family: serif;
}

// 下拉列表
.input-select {
  width: 100%;
  height: 30px;
  line-height: 30px;
  border: 1px solid #d3d3d3;

  option {
    font-size: 13px;
    color: $Theme-second-text;
  }

}

// 双色表格 IE8: :nth-child(2n) 不支持
.table-two-color {
  width: 100%;
  border: 1px solid #e4e4e4;

  th, td {
    text-align: left;
    padding: 20px;
    word-break: keep-all;
  }

  th {
    background-color: #ecf0f1;
    font-weight: normal;
  }

  tr:nth-child(2n) {
    background-color: #ecf0f1;
  }

}

// 没有查询结果
.not-find {
  font-size: 20px;
  color: black;
}

// 文本域
textarea {
    border: 1px solid gray;
    color: #2C3E50;
    font-size: 15px;
    line-height: 27px;
    padding: 10px;
    font-family: "PingFang SC","Microsoft yahei","Helvetica Neue",Helvetica,Arial,sans-serif;
    resize: none;
}

// 切换开关  IE8: background-size: cover; 不支持
.swich {

  span {
    display: none;
    border-radius: 4px;
    width: 60px;
    height: 23px;
    line-height: 23px;
    color: $Theme-link-text;
    background: url($PATH-Image-Dir + 'icons.png') no-repeat;
    background-size: cover;
  }

  .active {
    @extend %ib;
  }

  .on {
    background-position: 0 0;
    padding: 0 0 0 10px;
  }

  .off {
    background-position: -66px 0;
    padding: 0 0 0 22px;
  }

}


.i-switch {
  @extend %ib;
  width: 48px;
  height: 24px;
  line-height: 22px;
  border-radius: 24px;
  border: 1px solid #ccc;
  background-color: #ccc;
  position: relative;
  cursor: pointer;
  transition: all .2s ease-in-out;

  .switch-inner {
    color: #fff;
    font-size: 12px;
    position: absolute;
    left: 25px;
  }

  // TODO 禁用样式

  .switch-dot {
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background-color: #fff;
    position: absolute;
    left: 1px;
    top: 1px;
    cursor: pointer;
    transition: left .2s ease-in-out,width .2s ease-in-out;
  }


}

.i-switch.checked {
  border-color: #39f;
  background-color: #39f;
  .switch-inner {
    left: 8px;
  }
}
.i-switch.disabled {
  cursor: not-allowed;
  background: #f3f3f3;
  border-color: #f3f3f3;

  .switch-inner {
    color: #ccc;
  }
  .switch-dot {
    cursor: not-allowed;
  }
}

// 联想列表
.autocomplete-wrap {
  position: absolute;
  width: 100%;
  background-color: #fff;
  box-shadow: 1px 1px 11px rgba(0,0,0,.5);
  border: 1px solid rgba(0,0,0,0.2);
  overflow: auto;
  z-index: 50;

  li {
    @extend %ellipsis;
    padding: 10px;
    cursor: pointer;
  }

  li:hover, li.active {
    background-color: #999;
    color: #eee;
  }

}






// 小圆点
.dot {
  @extend %ib;
  $dot-size: 7px;
  width: $dot-size;
  height: $dot-size;
  border-radius: $dot-size;
  background: #e0dddd;
}





